// Products section

.products {
  padding-top: 80px;
  padding-bottom: 40px;
  @include mobile {
    padding-top: 40px;
    padding-bottom: 60px;
  }
  @include tablet {
    &__container {
      padding-left: 32px;
      padding-right: 32px;
    }
  }
  @include desktop {
    padding-top: 59px;
  }
  background-color: #ffffff;
}

.products__pretitle {
  display: block;
  text-align: center;
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  font-size: var(--small-title-size);
  line-height: 1.17;
  @include tablet {
    line-height: 1.15;
  }
  @include desktop {
    line-height: 1.13;
    letter-spacing: 0.04em;
  }
  text-transform: uppercase;
  color: var(--pretitle-color);
}

.products__title {
  display: block;
  margin-top: 15px;
  margin-bottom: 140px;
  text-align: center;
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  font-size: var(--big-title-size);
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--main-title-color);
  @include tablet {
    margin-bottom: 154px;
  }
  @include desktop {
    margin-bottom: 261px;
    line-height: 1.14;
    letter-spacing: 0.06em;
  }
}
// Product-cards

.products-cards-list {
  display: flex;
  flex-direction: column;
  row-gap: 140px;
  @include tablet {
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    column-gap: 16px;
  }
  @include desktop {
    column-gap: 32px;
  }
}

.products-card {
  display: flex;
  align-items: center;
  @include tablet {
    flex-basis: calc((100% - 32px) / 3);
  }
  @include desktop {
    flex-basis: calc((100% - 64px) / 3);
  }
  perspective: 2000px;
}

.card-box {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 2000ms;
  transform-style: preserve-3d;
}

.do-flip {
  transform: rotateY(180deg);
}

// Card design

.card-front,
.card-back {
  display: flex;
  flex-direction: column;
  position: relative;
  align-items: center;
  border-radius: 24px;
  flex-basis: 100%;
  padding-top: 160px;
  padding-bottom: 40px;
  width: 100%;
  height: 100%;
  padding-left: 40px;
  padding-right: 40px;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  @include tablet {
    margin-top: 0;
    margin-left: 0;
    margin-right: 0;
    flex-basis: 100%;
    height: 100%;
    padding-top: 118px;
    padding-bottom: 40px;
    padding-left: 32px;
    padding-right: 32px;
  }
  @include desktop {
    margin-top: 0;
    margin-left: 0;
    margin-right: 0;
    flex-basis: 100%;
    height: 100%;
    padding-top: 165px;
    padding-bottom: 60px;
    padding-left: 75px;
    padding-right: 75px;
  }
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.card-back {
  padding-top: 120px;
  @include tablet {
    padding-top: 95px;
  }
  @include desktop {
    padding-top: 125px;
  }
  transform: rotateY(180deg);
  position: absolute;
  top: 0;
}

.products-card:nth-child(1) .card-front,
.products-card:nth-child(1) .card-back {
  background-color: #ffb8ca;
}
.products-card:nth-child(2) .card-front,
.products-card:nth-child(2) .card-back {
  background-color: #f0d1a5;
}
.products-card:nth-child(3) .card-front,
.products-card:nth-child(3) .card-back {
  background-color: #c2e297;
}

// Picture on top

.products-card:nth-child(1) .card-front::before {
  background-image: url(/images/img-products/prod-pic1-mob-1x.png);
  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url(/images/img-products/prod-pic1-mob-2x.png);
  }
  @include tablet {
    background-image: url(/images/img-products/prod-pic1-tab-1x.png);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(/images/img-products/prod-pic1-tab-2x.png);
    }
  }
  @include desktop {
    background-image: url(/images/img-products/prod-pic1-desk-1x.png);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(/images/img-products/prod-pic1-desk-2x.png);
    }
  }
}

.products-card:nth-child(2) .card-front::before {
  background-image: url(/images/img-products/prod-pic2-mob-1x.png);
  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url(/images/img-products/prod-pic2-mob-2x.png);
  }
  @include tablet {
    background-image: url(/images/img-products/prod-pic2-tab-1x.png);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(/images/img-products/prod-pic2-tab-2x.png);
    }
  }
  @include desktop {
    background-image: url(/images/img-products/prod-pic2-desk-1x.png);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(/images/img-products/prod-pic2-desk-2x.png);
    }
  }
}

.products-card:nth-child(3) .card-front::before {
  background-image: url(/images/img-products/prod-pic3-mob-1x.png);
  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url(/images/img-products/prod-pic3-mob-2x.png);
  }
  @include tablet {
    background-image: url(/images/img-products/prod-pic3-tab-1x.png);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(/images/img-products/prod-pic3-tab-2x.png);
    }
  }
  @include desktop {
    background-image: url(/images/img-products/prod-pic3-desk-1x.png);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(/images/img-products/prod-pic3-desk-2x.png);
    }
  }
}

.card-front::before {
  content: '';
  display: block;
  position: absolute;
  background-repeat: no-repeat;
  background-size: contain;
  width: 280px;
  height: 280px;
  top: -140px;
  @include tablet {
    width: 224px;
    height: 224px;
    top: -138px;
  }
  @include desktop {
    width: 394px;
    height: 394px;
    top: -261px;
  }
}

// Information on card

.products-card__title {
  display: flex;
  position: relative;
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  font-size: var (--products-title-size);
  line-height: 1.15;
  @include desktop {
    font-size: var(--small-title-size);
    line-height: 1.13;
  }
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--secondary-title-color);
  margin-bottom: 70px;
  margin-right: auto;
  margin-left: auto;
}

.products-card__title::after {
  content: '';
  background-image: url(/images/img-products/dots.svg);
  display: block;
  position: absolute;
  bottom: -38px;
  left: 50%;
  background-repeat: no-repeat;
  width: 26px;
  height: 6px;
  transform: translate(-50%, -50%);
}

.products-card__text {
  display: flex;
  font-style: normal;
  font-weight: 700;
  font-size: var(--products-text-size);
  line-height: 1.52;
  margin-bottom: 32px;
  @include tablet {
    line-height: 1.76;
  }
  @include desktop {
    font-size: 16px;
  }
  text-align: center;
  letter-spacing: 0.04em;
  color: var(--thirdly-text-color);
}

.arrow-button {
  cursor: pointer;
  border: none;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: 0;
  @include tablet {
    margin-top: auto;
  }
  @include desktop {
    margin-top: auto;
  }
  align-items: center;
  justify-content: center;
  width: 40px;
  min-height: 40px;
  background-color: var(--thirdly-text-color);
  border-radius: 50%;
  transition: background-color 300ms ease;
}

.arrow-icon {
  fill: var(--main-btn-color);
  margin: auto;
  .arrow-button:hover & {
    fill: var(--modal-menu-bg-color);
  }
  .arrow-button:focus & {
    fill: var(--modal-menu-bg-color);
  }
}
