.customer-reviews-container {
  margin: 40px auto;
  max-width: 480px;
  @media screen and (min-width: 768px) {
    padding: 0;
    max-width: 768px;
  }
  @media screen and (min-width: 1280px) {
    max-width: 1280px;
  }
}
.swiper {
  max-width: 100%;
  height: 520px;
  background-color: var(--secondary-bg-color);

}
.text-wrapper {
  margin: 0 auto;
  padding: 30px 50px 25px;
  background-color: #fafafa;
  @media screen and (min-width: 768px) {
    padding: 37px 6px 32px;
    max-width: 620px;
  }
  @media screen and (min-width: 1280px) {
    box-sizing: border-box;
    max-width: 1034px;
    padding: 47px 106px 26px;
  }
}
.slider-img {
  display: block;
  margin: 0 auto 32px;
  @media screen and (min-width: 768px) {
    margin-bottom: 20px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 16px;
  }
}
.slider-text {
  position: relative;
  padding: 0px 15px 5px;

  font-weight: 500;
  font-size: 12px;
  line-height: 2.55;
  text-indent: 10px;
  text-align: center;
  color: var(--main-text-color);
  
  background: repeating-linear-gradient(#fafafa 0px, #fafafa 30px, #e1e1e1 31px);
  z-index: 1;
  @media screen and (min-width: 768px) {
    padding: 0px 30px 5px;
    font-size: 14px;
    line-height: 2.16;
    letter-spacing: 0.04em;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 1.86;
    padding: 0px 35px 5px;
  }


}
.slider-text:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(-50%);
  display: inline-block;
  width: 54px;
  height: 40px;
  background-image: url(../images/icon-quote.svg);
  background-size: cover;
  z-index: -1;
}
.signature {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.86;
  text-align: center;
  @media screen and (min-width: 768px) {
    font-size: 16px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 20px;
  }
}
.signature::before {
  content: '';
  display: block;
  margin: 16px auto;
  width: 32px;
  height: 4px;
  background-image: url(../images/icon-dots.svg);
  background-size: contain;
}
.swiper .swiper-pagination-bullet-active {
  background-image: url(../images/icon-home.svg);
  background-size: cover;
  border-radius: 0;
  background-color: inherit;
}
